import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  Container,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
} from "@mui/material";
import { useTheme } from "@mui/material/styles";
import axios from "axios";
import RegistrarTagPopup from "./RegistrarTagPopup"; // Importe o componente do pop-up

const PaginaCadastro = () => {
  const theme = useTheme();
  const [nome, setNome] = useState("");
  const [telefone, setTelefone] = useState("");
  const [email, setEmail] = useState("");
  const [cadastros, setCadastros] = useState([]);
  const [openPopup, setOpenPopup] = useState(false); // Controle do pop-up
  const [usuarioIdSelecionado, setUsuarioIdSelecionado] = useState(null); // ID do usurio

  useEffect(() => {
    carregarCadastros();
  }, []);

  const carregarCadastros = async () => {
    const response = await axios.get("http://localhost:8000/cadastros");
    setCadastros(response.data);
  };

  const handleCadastro = async () => {
    await axios.post("http://localhost:8000/cadastro", { nome, telefone, email });
    setNome("");
    setTelefone("");
    setEmail("");
    carregarCadastros();
  };

  const abrirPopupRegistroTag = (id) => {
    setUsuarioIdSelecionado(id);
    setOpenPopup(true);
  };

  const fecharPopupRegistroTag = () => {
    setOpenPopup(false);
    setUsuarioIdSelecionado(null);
    carregarCadastros(); // Atualiza a tabela aps o registro
  };

  return (
    <Container maxWidth="md">
      <Paper elevation={3} style={{ padding: theme.spacing(4), marginTop: theme.spacing(4) }}>
        <Typography variant="h4" align="center" gutterBottom>
          Cadastro de Usurios
        </Typography>

        <Box component="form" sx={{ display: "flex", flexDirection: "column", gap: 2, mb: 4 }}>
          <TextField
            label="Nome"
            variant="outlined"
            value={nome}
            onChange={(e) => setNome(e.target.value)}
            fullWidth
          />
          <TextField
            label="Telefone"
            variant="outlined"
            value={telefone}
            onChange={(e) => setTelefone(e.target.value)}
            fullWidth
          />
          <TextField
            label="Email"
            variant="outlined"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            fullWidth
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleCadastro}
            sx={{ alignSelf: "flex-end", mt: 2 }}
          >
            Cadastrar
          </Button>
        </Box>

        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell align="center" sx={{ fontWeight: "bold", color: theme.palette.primary.main }}>
                  Nome
                </TableCell>
                <TableCell align="center" sx={{ fontWeight: "bold", color: theme.palette.primary.main }}>
                  Telefone
                </TableCell>
                <TableCell align="center" sx={{ fontWeight: "bold", color: theme.palette.primary.main }}>
                  Email
                </TableCell>
                <TableCell align="center" sx={{ fontWeight: "bold", color: theme.palette.primary.main }}>
                  Acesso
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {cadastros.map((cadastro) => (
                <TableRow key={cadastro.id}>
                  <TableCell align="center">{cadastro.nome}</TableCell>
                  <TableCell align="center">{cadastro.telefone}</TableCell>
                  <TableCell align="center">{cadastro.email}</TableCell>
                  <TableCell align="center">
                    {cadastro.idAcesso === "No Cadastrado" ? (
                      <Button
                        variant="outlined"
                        color="secondary"
                        onClick={() => abrirPopupRegistroTag(cadastro.id)}
                      >
                        Cadastrar Tag
                      </Button>
                    ) : (
                      "Tag Cadastrada"
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>

      {/* Pop-up de registro de tag */}
      {openPopup && (
        <RegistrarTagPopup
          open={openPopup}
          onClose={fecharPopupRegistroTag}
          usuarioId={usuarioIdSelecionado}
          atualizarTabela={carregarCadastros}
        />
      )}
    </Container>
  );
};

export default PaginaCadastro;
