// components/AcessoTable.js
import React, { useState, useEffect } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Fade, Typography, Box } from '@mui/material';

function AcessoTable() {
  const [dados, setDados] = useState([]);
  const [novosRegistros, setNovosRegistros] = useState([]); // Armazena novos registros para aplicar o fade

  const atualizarTabela = () => {
    fetch('/atualizacao')
      .then((response) => {
        if (!response.ok) throw new Error('Erro na requisio');
        return response.json(); // API deve retornar JSON com dados
      })
      .then((data) => {
        const novos = data.filter((registro) => !dados.some((d) => d.nome === registro.nome && d.horario === registro.horario));
        setNovosRegistros(novos); // Armazena apenas os novos registros
        setDados(data); // Atualiza todos os dados
      })
      .catch((error) => console.error('Erro:', error));
  };

  useEffect(() => {
    atualizarTabela(); // Primeira atualizao
    const interval = setInterval(atualizarTabela, 5000); // Atualiza a cada 5 segundos
    return () => clearInterval(interval); // Limpa o intervalo ao desmontar o componente
  }, []);

  return (
    <Box display="flex" flexDirection="column" alignItems="center">
      <Typography variant="h4" color="primary" gutterBottom>
        Registros de Acesso
      </Typography>
      <TableContainer component={Paper} sx={{ maxWidth: 600 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell align="center" sx={{ color: 'primary.main', fontWeight: 'bold' }}>Nome</TableCell>
              <TableCell align="center" sx={{ color: 'primary.main', fontWeight: 'bold' }}>Horrio de Acesso</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {dados.map((registro, index) => (
              <Fade
                in={novosRegistros.includes(registro)} // Aplica o fade apenas nos novos registros
                timeout={{ enter: 500 }}
                key={index}
              >
                <TableRow>
                  <TableCell align="center">{registro.nome}</TableCell>
                  <TableCell align="center">{registro.horario}</TableCell>
                </TableRow>
              </Fade>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
}

export default AcessoTable;
